import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import child from './child'
const myPlugin = store => {
    console.log('初始化...');
    // 当 store 初始化后调用
    store.subscribe((mutation, state) => {
        console.log('mutation....');
      // 每次 mutation 之后调用
      // mutation 的格式为 { type, payload }
    })
  }
export default new Vuex.Store({
    state:{
        count:100,
        todos:[
            {id:1,done:false},
            {id:2,done:true},
            {id:3,done:false},
            {id:4,done:true},
        ]
    },
    getters:{
        doneTodo(state){
            return state.todos.filter(todo => todo.done)
        },
        doneLength(state,getters){
            return getters.doneTodo.length;
        }
    },
    mutations:{
      increment(state,count){
          state.count+=count;
      },
      bar(state,str){
          console.log(str)
      }  
    },
    actions:{
        print({commit,dispatch}){
            commit('increment',5);
            dispatch('foo',6666);
        },
        foo({commit},str){
           commit('bar',str)
        }
    },
    modules:{
        test:child
    },
    plugins: [myPlugin]
})


