import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)
const Foo = { template: '<div style="width:200px;height:200px;background:red;">foo</div>' }
const Bar = { template: '<div style="width:200px;height:200px;background:blue;">bar</div>' }
const User = {
  template: '<div>{{newProp}},{{id}}</div>',
  props: ['newProp', 'id']
};


const route = new Router({
  routes: [
    {
      path: '/',
      components: {
        default: { template: '<div>ppp</div>' },
      }
    },
    {
      path: '/bar', component: Bar, name: 'bar', children: [
        {
          path: 'foo', component: {
            template: '<h1>test</h1>',
          }, meta: {
            requireAuth: true
          }
        }
      ]
    },
    { path: '/foo', component:Foo },
    { path: '/user/:id', component: User, props: { newProp: 666666 } },
    { path: '/ppp', redirect: { name: 'bar' } },
    {
      path: '/go/:id', component: {
        template: '<div>{{$route.params}}</div>'
      }
    }
  ]
})
export default route;


