import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default {
    namespaced:true,
    state:{
        count:999
    },
    mutations:{
        print(state,num){
            console.log(num);
        }
    },
    actions:{
        getCount({state,commit,rootState}){
            commit('print',state.count+rootState.count) 
        },
        getNum({commit}){
            commit('bar','hello',{root:true});
        }
    }
}


